@tailwind base;

@tailwind components;

.link {
  @apply text-indigo-500;
}

.link:hover {
  @apply underline;
}

.link:focus {
  @apply outline-none underline;
}

.input {
  @apply block w-full border mt-1 p-2 rounded;
}

.input:focus {
  @apply outline-none border-indigo-500;
}

.button {
  @apply border border-transparent bg-indigo-500 text-white font-bold py-2 px-4 rounded;
}

.button:hover {
  @apply bg-indigo-400;
}

.button:focus {
  @apply outline-none bg-indigo-600;
}

@tailwind utilities;
